.calculator {
	height: 100vh;
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}

.visor {
	width: 318px;
	min-height: 100px;
	border: #b3b3b3 2px solid;
	border-bottom: none;
	color: white;
	display: flex;
	flex-direction: column;
	align-items: center;
}
.acc {
	width: 97%;
	min-height: 30px;
	display: flex;
	justify-content: flex-end;
	align-items: flex-end;
	font-size: 20px;
	color: #b3b3b3;
}

.total {
	width: 97%;
	height: 70px;
	display: flex;
	justify-content: flex-end;
	align-items: flex-end;
	font-size: 60px;
}

.container {
	width: 320px;
	height: 400px;
	display: flex;
	flex-wrap: wrap;
	border: #b3b3b3 1px solid;
}

/*----------inputs----------*/

.button {
	width: 80px;
	height: 80px;
	border: #b3b3b3 1px solid;
	background-color: #535353;
	color: white;
	font-size: 25px;
	transition: background-color 230ms ease-out;
}
.button:hover {
	background-color: #383838;
}

.zero {
	width: 160px;
}

.equal {
	height: 160px;
	background-color: #1db954;
	margin-top: -80px;
}
.equal:hover {
	background-color: #0e993f;
}

.action {
	background-color: #212121;
}
.action:hover {
	background-color: #0a0a0a;
}
